<template>
 <div>
    <div class="container">
      <div class="letsfigure-title">
        <h2>Letâs figure out how much you can afford</h2>
        <p>Before you start shopping, letâs figure out how much you can afford. Move the sliders to see how the loan duration and the total loan amount affect your monthly payments.</p>
      </div>
      <div class="letsfigure-main">
        <div class="letsfigure-main_left">
          <div class="left_buttons">
            <button class="button-lestfigure1 button" :class="{btnactive: actBTN === 'btn1'}"  @click="inputMaxPoor">Poor</button>
            <button class="button-lestfigure2 button" :class="{btnactive: actBTN === 'btn2'}" @click="inputMaxAverage">Average</button>
            <button class="button-lestfigure3 button" :class="{btnactive: actBTN === 'btn3'}" @click="inputMaxGood">Good</button>
          </div>
          <div class="left_loan-amount">
            <div class="loan-amount_top">
              <h4>Loan Amount</h4>
              <span>$ {{loanAmount}}</span>
            </div>
            <div class="loan-amount_range">
              <input type="range" min="10000" :max="max" step="1000" 
              v-model='loanAmount'
              @input="updateSlider2" :style="{backgroundSize: backgroundSize2}">
            </div>
          </div>
          <div class="left_loan-amount">
            <div class="loan-amount_top">
              <h4>Loan Duration</h4>
              <span>{{loanDuratin}} <span>month</span></span>
            </div>
            <div class="loan-amount_range">
              <input type="range" min="6" max="60" step="2" 
              v-model='loanDuratin'
              @input="updateSlider" :style="{backgroundSize: backgroundSize}">
            </div>
          </div>
          
        </div>
        <div class="letsfigure-main_right">
          <div class="right-wrapper">
            <div class="right-main">
              <h5>Bi-Weekly Payment</h5>
              <span class="right-main_big">$ {{ uptadeBiWeeklyPayment }}</span>
            </div>
            <div class="right-main_bottom">
              <div class="right-main">
                <h5>Monthly Payment</h5>
                <span class="right-main_small" >$ {{ uptadeMonthlyPayment }}</span>
              </div>
              <div class="right-main">
                <h5>Weekly Payment</h5>
                <span class="right-main_small">$ {{ uptadeWeeklyPayment }}</span>
              </div>
            </div>
          </div>
          <router-link to="" class="letsfigure-require">Request a car</router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      loanAmount: 10000,
      loanDuratin: 6,
      BiWeeklyPayment: null,
      MonthlyPayment: null,
      WeeklyPayment: null,
      backgroundSize: '0% 100%',
      backgroundSize2: '0% 100%',
      max: 60000,
      actBTN: 'btn1',
    }
  },

  methods: {
     updateSlider(e) {
            let clickedElement = e.target,
                min = clickedElement.min,
                max = clickedElement.max,
                val = clickedElement.value;

            this.backgroundSize = (val - min) * 100 / (max - min) + '% 100%';
     },
     updateSlider2(e) {
            let clickedElement = e.target,
                min = clickedElement.min,
                val = clickedElement.value;
                console.log(val, this.max, min)
            this.backgroundSize2 = (val - min) * 100 / (this.max - min) + '% 100%';
     },
     inputMaxPoor(){
      this.actBTN = 'btn1'
      this.max = 60000;
      if(this.backgroundSize2 == '100% 100%'){
        this.loanAmount = this.max
      }
     },
     inputMaxAverage(){
      this.actBTN = 'btn2'
      this.max = 80000;
      if(this.backgroundSize2 == '100% 100%'){
        this.loanAmount = this.max
      }
     },
     inputMaxGood(){
      this.actBTN = 'btn3'
      this.max = 100000;
      // if(this.backgroundSize2 == '100% 100%'){
      //   this.loanAmount = this.max
      // }
     }
     
   },

   computed: {
      uptadeMonthlyPayment: function(){
        return this.MonthlyPayment = Math.round(this.loanAmount / this.loanDuratin);
      }, 
      uptadeBiWeeklyPayment: function(){
        return this.BiWeeklyPayment = Math.round((this.loanAmount / this.loanDuratin) / 2);
      },
      uptadeWeeklyPayment: function(){
        return this.WeeklyPayment = Math.round((this.loanAmount / this.loanDuratin) / 4);
      }
    },

    watch: {
      max(newValue){

      }
    },
}
</script>
